<template>
    <div>
        <el-table
                :data="userList"
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label="ID"
                    width="200">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="auth"
                    label="权限"
                    width="100"
                    :filters="[{text: '超级管理员', value: 0}, {text: '普通用户', value: 1}]"
                    :filter-method="filterHandler">
                <template slot-scope="scope">
                    <span v-if="scope.row.auth === 0">超级管理员</span>
                    <span v-if="scope.row.auth === 1">普通用户</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="isFobiden"
                    label="封禁情况"
                    width="100"
                    :filters="[{text: '否', value: 0}, {text: '是', value: 1}]"
                    :filter-method="filterHandler">
                <template slot-scope="scope">
                    <span v-if="scope.row.isFobiden === 0">否</span>
                    <span v-if="scope.row.isFobiden === 1">是</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="beginTime"
                    label="授权开始时间"
                    width="180"
                    :formatter="timeFormat">
            </el-table-column>
            <el-table-column
                    prop="overTime"
                    label="授权结束时间"
                    width="180"
                    :formatter="timeFormat">
            </el-table-column>
            <el-table-column
                    prop="citizenId"
                    label="身份证号码">
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100">
                <template slot-scope="scope">
                    <el-button @click="handleModify(scope.row.id)" type="text" size="small">修改</el-button>
                    <el-divider direction="vertical"></el-divider>
                    <el-button type="text" size="small" v-if="scope.row.isFobiden === 0" @click="handleBan(1, scope.row.id)">封禁</el-button>
                    <el-button type="text" size="small" v-if="scope.row.isFobiden === 1" @click="handleBan(0, scope.row.id)">解封</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="修改用户信息" :visible.sync="updateUserDialogVisible" v-on:close="handleClose">
            <el-form ref="updateUserForm" :model="updateUserData" size="medium" label-width="110px"
                     label-position="left">
                <el-form-item label="用户ID" prop="id" required>
                    <el-input v-model="updateUserData.id" placeholder="请输入用户ID" readonly :disabled='true' clearable
                              :style="{width: '100%'}"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password" required>
                    <el-input v-model="updateUserData.password" placeholder="请输入密码" clearable :style="{width: '100%'}">
                    </el-input>
                </el-form-item>
                <el-form-item label="名称" prop="name" required>
                    <el-input v-model="updateUserData.name" placeholder="请输入名称" clearable :style="{width: '100%'}">
                    </el-input>
                </el-form-item>
                <el-form-item label="身份证号码" prop="citizenId" required>
                    <el-input v-model="updateUserData.citizenId" placeholder="请输入身份证号码" show-word-limit clearable
                              :style="{width: '100%'}"></el-input>
                </el-form-item>
                <el-form-item label="权限" prop="auth" required>
                    <el-switch v-model="updateUserData.auth" active-text="超级管理员" inactive-text="普通用户" :active-value='0'
                               :inactive-value='1'></el-switch>
                </el-form-item>
                <el-form-item label="是否封禁" prop="isFobiden" required>
                    <el-switch v-model="updateUserData.isFobiden" active-text="是" inactive-text="否" :active-value='1'
                               :inactive-value='0'></el-switch>
                </el-form-item>
                <el-form-item label="授权开始时间" prop="beginTime" required>
                    <el-date-picker type="datetime" v-model="updateUserData.beginTime" format="yyyy-MM-dd HH:mm:ss"
                                    value-format="yyyy-MM-dd HH:mm:ss" :style="{width: '100%'}" placeholder="请选择授权开始时间" clearable readonly
                                    :disabled='true'></el-date-picker>
                </el-form-item>
                <el-form-item label="授权结束时间" prop="overTime" required>
                    <el-date-picker type="datetime" v-model="updateUserData.overTime" format="yyyy-MM-dd HH:mm:ss"
                                    value-format="yyyy-MM-dd HH:mm:ss" :style="{width: '100%'}" placeholder="请选择授权结束时间" clearable readonly :disabled='true'>
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="handleClose">取消</el-button>
                <el-button type="primary" @click="handelConfirm">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { userAPI } from "../../globalAPI";
    import { renderTime } from "../../utils";
    export default {
        name: "index",
        data() {
            return {
                userList: [],
                updateUserData: {},
                updateUserDialogVisible: false
            }
        },
        methods: {
            /**
             * ajax获取用户列表
             */
            getUserList() {
                this.axios.post(userAPI.getAllUser)
                        .then((response) => {
                          if(response.data.resultCode === 1){
                            this.userList = response.data.objData;
                          }
                        })
                        .catch((error) => {
                            this.$message({
                                type: "error",
                                message: error.response.data.message
                              });
                        });
            },
            /**
             * element 筛选方法
             * @param value 值
             * @param row 行
             * @param column 列
             * @returns {boolean} 返回筛选后的内容
             */
            filterHandler(value, row, column) {
                const property = column['property'];
                return row[property] === value;
            },
            /**
             * 对后端传来的时间转化为前端所显示的格式
             * @param row 行
             * @param column 列
             * @param cellValue 单元格数值
             * @param index index
             * @returns {string} 格式化后的内容
             */
            timeFormat(row, column, cellValue, index) {
                return renderTime(cellValue);
            },
            /**
             * 处理点击封禁、解封按钮时触发的操作
             * @param isBan
             * @param id
             */
            handleBan(isBan, id) {
                // ajax请求
                this.axios.post(userAPI.ban, this.qs.stringify({
                    id: id,
                    isBan: isBan
                }))
                        .then((response) => {
                          if(response.data.resultCode === 1){
                              // 重新获取一次
                              this.getUserList();
                              this.$message({
                                  type: "success",
                                  message: response.data.message
                              });
                          }
                        })
                        .catch((error) => {
                            this.$message({
                                type: "error",
                                message: error.response.data.message
                              });
                        });
            },
            /**
             * 处理点击修改按钮时触发的操作
             * @param id
             */
            handleModify(id) {
                // 找到对应的 userData 初始化 Dialog 赋值
                let index = this.userList.findIndex((value) => {
                    return value.id === id;
                });
                this.updateUserData = this.userList[index];
                // 显示 Dialog
                this.updateUserDialogVisible = true;
            },
            /**
             * 处理点击修改 Dialog 内确定按钮触发的操作
             */
            handelConfirm() {
                // 对表单进行核验
                this.$refs.updateUserForm.validate((valid) => {
                    if (valid) {
                        console.log(this.updateUserData);
                        this.axios.post(userAPI.updateUserMsg, this.qs.parse({
                            id: this.updateUserData.id,
                            password: this.updateUserData.password,
                            auth: this.updateUserData.auth,
                            isFobiden: this.updateUserData.isFobiden,
                            beginTime: this.updateUserData.beginTime,
                            overTime: this.updateUserData.overTime,
                            UUID: this.updateUserData.UUID,
                            name: this.updateUserData.name,
                            citizenId: this.updateUserData.citizenId
                        }))
                                .then((response) => {
                                  if(response.data.resultCode === 1){
                                     // 重新获取一次
                                     this.getUserList();
                                     this.$message({
                                         type: "success",
                                         message: response.data.message
                                     });
                                     this.updateUserDialogVisible = false;
                                  }
                                })
                                .catch((error) => {
                                    this.$message({
                                        type: "error",
                                        message: error.response.data.message
                                      });
                                });
                    } else {
                        return false;
                    }
                });
            },
            /**
             * 处理点击修改 Dialog 内关闭按钮触发的操作
             */
            handleClose() {
                // 数据置空
                this.updateUserData = {};
                // 清除检验
                this.$refs.updateUserForm.resetFields();
                // Dialog 关闭
                this.updateUserDialogVisible = false;
            }
        },
        created() {
            this.getUserList();
        }
    }
</script>

